 /**todo*/
  .todolist-container{
    background-image: linear-gradient(120deg, #83C5BE 0%, #006D77 100%);
    color: white;
    font-family: "Poppins", sans-serif;
    min-height: 100vh;
  }
  /*
  .todo-input{
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    background: white;
  }
  .todo-button{
    padding: 0.5rem;
