  */
  .todo-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
   
    
  }
  
  .todo-list {
    
    list-style: none; 
  }
  
  .todo {
    margin: 0.5rem;
    background: white;
    font-size: 1.5rem;
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 1s ease;
    width: 400px;    
    
  }
  .filter-todo {
    padding: 1rem;
  }
  .todo li {
    flex: 1;
  }
  .trash-btn,
    .complete-btn {
      background: #ff6f47;
      color: white;
      border: none;
      padding: 1rem;
      cursor: pointer;
      font-size: 1rem;
    }
    .complete-btn {
      background: rgb(131, 197, 190);
    }
 
    .todo-item {
      padding: 0rem 0.5rem;
    }
    
    .fa-trash,
    .fa-check {
      pointer-events: none;
    }
    
    .fall {
      transform: translateY(10rem) rotateZ(20deg);
      opacity: 0;
    }
  
    .completed {
      text-decoration: line-through;
      opacity: 0.5;
    }
  
    /*CUSTOM SELECTOR */
    select {
      -webkit-appearance: none;
      -moz-appearance: none;
      -ms-appearance: none;
      appearance: none;
      outline: 0;
      box-shadow: none;
      border: 0 !important;
      background-image: none;
    }
  
    /* Custom Select */
    .select {
      margin: 1rem;
      position: relative;
      overflow: hidden;
    }
    select {
      color: #ff6f47;
      font-family: "Poppins", sans-serif;
      cursor: pointer;
      width: 12rem;
      font-size: 1rem;
    }
    /* Arrow */
    .select::after {
      content: "\25BC";
      position: absolute;
      top: 0;
      right: 0;
      padding: 1rem;
      background: #ff6f47;
      cursor: pointer;
      pointer-events: none;
    }
  
  .login-container {
    display: flex;
    justify-content: center;
    align-items: center;
  
  }
  
  .login-input {
    min-height: 7vh;
    margin : 1rem;
    justify-content: center;
    align-items: center;
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    background: white;
  
  }
  
  .login-btn {
    min-height: 7vh;
    padding: 0.5rem;
    font-size: 2rem;
    border: none;
    background: white;
    color: #ff6f47;
    cursor: pointer;
    transition: all 0.3s ease;
  
  }
